<template>
  <div class="row m-4">
    <div class="col-lg-12 p-0 card">
      <div class="card-header">Yorumlar Listesi (T端m端)</div>
      <div class="card-body">
        <div v-for="item in yazi" :key="item._id">
          <h2>{{item.Baslik}}</h2>
          <div v-for="yorum in item.Yorumlar" :key="yorum._id">
            <yorum v-bind="item.Yorumlar" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Yorum from "../../components/Yorum.vue";

export default {
  name: "Yorumlar",
  data: () => {
    return { yazi: [] };
  },
  components: {
    Yorum
  },
  methods: {
    async veriCek() {
      await fetch(process.env.VUE_APP_SERVER_URL + "yorumlar")
        .then(res => {
          return res.json();
        })
        .then(res => {
          this.yazi = res;
        });
    }
  },
  created() {
    this.veriCek();
  }
};
</script>

<style scoped>
thead {
  cursor: pointer;
}
</style>